.lobby {
  height: 100vh;
  background-color: #63bcff;
  display: block;
  max-height: calc(90vh);
  padding-bottom: 10vh;
}

.lobby-content {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.lobby-content > div {
  display: flex;
}

.lobby-settings {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
  background: white;
}

.lobby-players {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
  background: white;
}
.lobby-start-row {
  display: flex;
  flex-direction: row;
  border-radius: 15px;
  justify-content: space-between;
  margin: 10px;
  padding: 10px;
  background: white;
  margin: 10px;
}
