.keyboard-detector {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
}
.keyboard-detector h1 {
  color: rgb(70, 70, 70);
}

.keyboard {
  display: flex;
  flex-wrap: wrap;
}
.keyboard div {
  width: calc(10% - 20px);
  aspect-ratio: 1/1;
  outline: 1px solid;
  border-radius: 50%;
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.active {
  background: lime;
}
