.file-explorer {
  height: 90vh;
  background-color: #aaa;
  max-height: calc(90vh);
  padding-bottom: 10vh;
}

.file-explorer-content {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.file-explorer-files {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}

.file-explorer-settings {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
  background: white;
}

.file-explorer-players {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
  background: white;
}

.file-explorer-start-row {
  display: flex;
  flex-direction: row;
  border-radius: 15px;
  justify-content: space-between;
  margin: 10px;
  padding: 10px;
  background: white;
  margin: 10px;
}

.file {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-radius: 15px;
  background: white;
  margin: 10px;
  width: 100%;
  cursor: pointer;
}

.file-actions {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-radius: 15px;
  background: white;
  margin: 10px;
}
