.news-feed {
  height: 100vh;
  background-color: hsl(162, 74%, 59%);
  display: block;
}
.header {
  height: 10vh;
  display: flex;
  justify-content: space-around;
}
.header-title {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.newspad {
  background-color: rgba(255, 255, 255, 0.301);
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  width: 100%;
}
.newspad:hover {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 0 0 rgba(200, 200, 200, 0.4);
  transform: scale(1);
  /* animation: pulse 2s infinite; */
}
.newspad.fs:hover {
  background-color: rgb(10, 241, 164);
  box-shadow: 0 0 0 0 rgba(200, 200, 200, 0.4);
  transform: scale(1);
  /* animation: pulse 2s infinite; */
}
.news-feed-content-item {
  margin: 50px;
  padding: 50px;
  background: rgb(243, 243, 243);
  color: rgb(51, 51, 51);
}
