.serverlist-container {
  background: rgb(240, 240, 240);
  color: black;
  max-height: 90vh;
  height: 90vh;
  padding-top: 10vh;
}
.serverlist-container .header {
  background-color: rgb(186, 175, 175);
}
.serverlist-list {
  margin: 20px;
  padding: 20px;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.serverlist-item {
  padding: 10px;
  display: flex;
  width: 100%;
}
.serverlist-item > div {
  width: 25%;
}

.serverlist-item:hover {
  background-color: rgba(255, 255, 255, 0.6);
}
.serverlist-item:nth-child(1) {
  background-color: rgba(255, 255, 255, 0.9);
}
.row {
  margin: 20px;
  padding: 20px;
  display: flex;
}
.central {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
}
.serverlist-item-join:hover {
  color: rgb(20, 198, 20);
}
