.section {
  background: rgb(240, 240, 240);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.section-child {
  flex-grow: 1;
}

.form-editor {
  color: black;
  background: rgb(240, 240, 240);
  font-size: 0.6em;
  padding-bottom: 40px;
  min-height: calc(90vh - 40px);
  padding-left: 15%;
}

.form-editor h1 {
  margin-left: 20px;
}

.form-editor-categories {
  position: fixed;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  background: white;
  top: 10vh;
  font-size: 1em;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  padding: 40px;
  z-index: 9999;
}

.form-editor-categories a {
  color: black;
  width: 50%;
}

.form-editor-categories a:hover {
  color: black;
  text-decoration: underline;
}

/* this is for checkbox booleans */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The switch-slider */
.switch-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  /* -webkit-transition: 0.4s; */
  /* transition: 0.4s; */
}

.switch-slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  /* -webkit-transition: 0.4s; */
  /* transition: 0.4s; */
}

input:checked + .switch-slider {
  background-color: limegreen;
}

input:focus + .switch-slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .switch-slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Mouse-over effects */
.slider:hover {
  opacity: 1; /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  width: 34px; /* Set a specific slider handle width */
  height: 34px; /* Slider handle height */
  background: white; /* Green background */
  cursor: pointer; /* Cursor on hover */
  border-radius: 50px;
}

/* number range slider */
/* The slider itself */
.slider {
  -webkit-appearance: none; /* Override default CSS styles */
  appearance: none;
  width: 90%; /* Full-width */
  height: 40px;
  background: #2196f3; /* Grey background */
  outline: none; /* Remove outline */
  margin-top: 20px;
  padding-left: 4px;
  padding-right: 4px;
  border-radius: 30px;
}

.mod-field {
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
  background: white;
  width: calc(100% - 40px);
}

.mod-field-label {
  margin-top: -10px;
  font-size: 1.7em;
}

#title {
  padding-top: 10px;
}

.read-only-value {
  margin-left: 0px !important;
  padding: 0;
  color: lightgray;
}

.hidden {
  display: none;
}

.form-editor-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 15%;
  height: 90vh;
  background: white;
  box-shadow: 0px 9px 10px 0px rgba(0, 0, 0, 0.5);
  margin-left: -15%;
  position: fixed;
}

.form-editor-sidebar a {
  color: black;
  font-size: 2em;
  height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form-editor-sidebar a:hover {
  background: rgb(240, 240, 240);
  text-decoration: underline;
}

.jump {
  height: 10vh;
  visibility: hidden;
}
