body {
  margin: 0;
  padding: 0;
  height: 100vh;
  font-family: "soft", sans-serif;
  font-size: 1.5em;
  color: white;
}
.background {
  background: white;
}

.flex {
  display: flex;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
ul a {
  text-decoration: none;
}

@font-face {
  font-family: "soft";
  src: url("./Dongle-Regular.ttf");
}
@font-face {
  font-family: "open sans italic";
  src: url("./OpenSans-Italic.ttf");
}
@font-face {
  font-family: "open sans";
  src: url("./OpenSans.ttf");
}

input[type="text"],
input[type="number"] {
  border-radius: 30px;
  border: 1px solid gray;
  padding: 10px;
  font-size: 1em;
  width: 90%;
}

.radio {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.settings-content {
  padding: 10px;
}
button {
  border-radius: 10px;
  border: none;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  font-size: 1em;
}
select {
  border: none;
  width: 100%;
  background: white;
}
.app {
  height: 100vh;
  min-height: 100vh;
}
.flex {
  display: flex;
  align-items: center;
  text-align: center;
}
nav {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 999;
}
.content {
  padding-top: 10vh;
}

.white-text {
  color: hsl(0, 0%, 100%);
}
.black-text {
  color: hsl(0, 0%, 4%);
}
.light-text {
  color: hsl(0, 0%, 96%);
}
.dark-text {
  color: hsl(0, 0%, 21%);
}
.primary-text {
  color: hsl(171, 100%, 41%);
}
.link-text {
  color: hsl(217, 71%, 53%);
}
.info-text {
  color: hsl(204, 86%, 53%);
}
.success-text {
  color: hsl(141, 71%, 48%);
}
.warning-text {
  color: hsl(48, 100%, 67%);
}
.danger-text {
  color: hsl(348, 100%, 61%);
}

.white {
  background: hsl(0, 0%, 100%);
  color: black;
}
.black {
  background: hsl(0, 0%, 4%);
  color: white;
}
.light {
  background: hsl(0, 0%, 96%);
  color: black;
}
.dark {
  background: hsl(0, 0%, 21%);
  color: white;
}
.primary {
  background: hsl(171, 100%, 41%);
  color: white;
}
.link {
  background: hsl(217, 71%, 53%);
  color: white;
}
.info {
  background: hsl(204, 86%, 53%);
  color: white;
}
.success {
  background: hsl(141, 71%, 48%);
  color: white;
}
.warning {
  background: hsl(48, 100%, 67%);
  color: white;
}
.danger {
  background: hsl(348, 100%, 61%);
  color: white;
}

select {
  font-size: 1.5em;
}
input[type="button"]:focus {
  outline: none;
  box-shadow: none !important;
  border: none;
}
input[type="button"] {
  outline: none !important;
  box-shadow: none !important;
}
