.desktop-main {
  font-size: 1.8em;
  height: 100vh;
  text-align: center;
  background-image: radial-gradient(
    circle,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0.5) 120%
  );
  letter-spacing: 6px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "local-game local-game local-game settings settings"
    "online-game online-game online-game news news"
    "mods mods tutorial tutorial exit";
}

.pad {
  background-color: rgba(255, 255, 255, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  transition: background-color 0.3s ease-in-out;
}
.pad:hover {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 0 0 rgba(200, 200, 200, 0.4);
}
.local-game {
  grid-area: local-game;
}

.online-game {
  grid-area: online-game;
}

.mods {
  grid-area: mods;
}

.settings {
  grid-area: settings;
}

.exit {
  grid-area: exit;
}

.tutorial {
  grid-area: tutorial;
}

.news {
  grid-area: news;
}
.fs {
  background: rgba(0, 0, 0, 0);
  fill: white;
  grid-area: fs;
}
.fs:hover {
  background: rgba(0, 0, 0, 0);
}

a {
  text-decoration: none;
  color: white;
}
.exit {
  background-color: hsla(00+00, 50%, 60%, 0.7);
}
.exit:hover {
  background-color: hsla(00+10, 60%, 50%, 0.9);
}

.tutorial {
  background-color: hsla(10+20, 50%, 60%, 0.7);
}
.tutorial:hover {
  background-color: hsla(10+30, 60%, 50%, 0.9);
}

.news {
  background-color: hsla(20+40, 50%, 60%, 0.7);
}
.news:hover {
  background-color: hsla(20+50, 60%, 50%, 0.9);
}

.settings {
  background-color: hsla(30+60, 50%, 60%, 0.7);
}
.settings:hover {
  background-color: hsla(30+70, 60%, 50%, 0.9);
}

.mods {
  background-color: hsla(40+80, 50%, 60%, 0.7);
}
.mods:hover {
  background-color: hsla(40+90, 60%, 50%, 0.9);
}

.online-game {
  background-color: hsla(50+100, 50%, 60%, 0.7);
}
.online-game:hover {
  background-color: hsla(50+110, 60%, 50%, 0.9);
}

.local-game {
  background-color: hsla(60+120, 50%, 60%, 0.7);
}
.local-game:hover {
  background-color: hsla(60+130, 60%, 50%, 0.9);
}
