.boot {
  height: 100vh;
  font-family: "bootfont";
  font-size: 23px;
  user-select: none;
}
.bootline {
  display: block;
}
.bootlogo-wrapper {
  align-items: center;
  justify-content: center;
}
.bootlogo {
  width: 100px;
  aspect-ratio: 1/1;
}

@font-face {
  font-family: "bootfont";
  src: url("../VT323-Regular.ttf");
}
img {
  image-rendering: pixelated;
}
