#mod-editor {
  font-weight: 500;
}
#mod-editor-tabs {
  display: flex;
  height: 10vh;
}
.navpad {
  background-color: rgb(255, 233, 105);
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  width: 100%;
}
.navpad:hover {
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 0 0 0 0 rgba(200, 200, 200, 0.4);
  transform: scale(1);
  /* animation: pulse 2s infinite; */
}
.navpad.fs:hover {
  background-color: rgb(10, 241, 164);
  box-shadow: 0 0 0 0 rgba(200, 200, 200, 0.4);
  transform: scale(1);
  /* animation: pulse 2s infinite; */
}
.navpad.fs {
  fill: black !important;
}
.file-dropdown {
  color: black;
  position: absolute;
  z-index: 999;
  background: beige;
  width: 30%;
  display: flex;
}
.json-editor {
  color: black;
}
